import '../../styles/templates/page.reviews.css';
var isPlaying = false;

$(document).ready(function() {

  $('.one-time').slick({
    dots: false,
    infinite: true,
    speed: 300,
    autoplay: false,
    slidesToShow: 3,
    centerPadding: '0px',
    arrows: false,
    centerMode: true,
    responsive: [{
      breakpoint: 768,
      settings: {
        arrows: false,
        centerMode: true,
        centerPadding: '60px',
        slidesToShow: 1,
      },
    }],
  });

  $('#prev').click(function() {
    $('.one-time').slick('slickPrev');
    var vid = parseInt($('.slick-center').find('video').attr('id').replace('video-', ''));
    playvideo(vid);
  });

  $('#next').click(function() {
    $('.one-time').slick('slickNext');
    var vid = parseInt($('.slick-center').find('video').attr('id').replace('video-', ''));
    playvideo(vid);
  });

  $('.slick-current').click(function() {
    var id = parseInt($('.slick-center').find('video').attr('id').replace('video-', ''));
    playvideo(id, true);
  });
  $('#socialSlide').slick({
    infinite: true,
    speed: 500,
    fade: true,
    cssEase: 'linear',
    prevArrow: $('#socialprev'),
    nextArrow: $('#socialnext'),
  });

  $('.one-time-quote').slick({
    dots: false,
    infinite: true,
    speed: 300,
    arrows: false,
    autoplay: false,
    slidesToShow: 3,
    responsive: [{
      breakpoint: 768,
      settings: {
        slidesToShow: 1,
      },
    }],
  });
});

function playvideo(id, stop_toggle = false) {
  var total_video_count = $('video[id*=\'video\']').length;

  if (isNaN(id)) {
    id = total_video_count;
  }

  if (stop_toggle == true) {
    isPlaying = !isPlaying;
  }

  console.log('id = ' + id);
  console.log('isPlaying = ' + isPlaying);

  var next = id + 1;
  var prev = id - 1;
  if (next > total_video_count) {
    next = 1;
  }
  if (prev <= 0) {
    prev = total_video_count;
  }

  $('#video-' + next)[0].pause();
  $('#video-' + prev)[0].pause();
  if (isPlaying == true) {
    $('#video-' + id)[0].play();
    $('.my-container .slick-current .playpause').hide();
  } else {
    $('.my-container .slick-current .playpause').show();
  }
}

$('#next-quote').click(function() {
  $('.one-time-quote').slick('slickNext');
});
$('#prev-quote').click(function() {
  $('.one-time-quote').slick('slickPrev');
});

function isInView(el) {
  var rect = el.getBoundingClientRect();
  return !(rect.top > $(window).height() || rect.bottom < 0);
}

$(document).on('scroll', function() {
  $('.my-container .slick-current video').each(function() {
    if (!isInView($(this)[0])) {
      id = $('.my-container .slick-current video').attr('data-id');
      $('#video-' + id)[0].pause();
    }
  });
});